import React, { Component } from "react";
import { connect } from "react-redux";
import { getData } from "../../actions/index";
import { getUser } from "../../actions/index";
import Header from "../../componets/header/header";
import Footer from "../../componets/footer/footer";
import BaseSelect from "../../componets/select/select";
import UserCard from "../../componets/userCard/userCard";
import CssBaseline from "@material-ui/core/CssBaseline";
import "./userPage.css";

class UserPage extends Component {
  state = {
    userName: '',
    inputSearch:'',
    expanded: false,
  };

  componentDidMount() {
    this.props.getData();
  }

  renderSelectOptions = () => {
    return this.props.users.map(dt => (
      <option key={dt.id} value={dt.username}>
        {dt.username}
      </option>
    ));
  };

  handleChange = ({ target: { value } }) => {
    this.setState({
      userName: value,
      expanded: false
    });
    this.props.getUser(this.props.users.find((elem)=> elem.username === this.state.userName ));
  };

  handleChangeSearch = ({ target: { value } }) => {
    this.setState({
      inputSearch: value
    });
  };

  handleExpandClick = () => {
    this.setState(state => ({ expanded: !state.expanded }));
  };

  findUser = ({ key }) => {    
    if( key === 'Enter'){
      this.setState({
        userName: this.state.inputSearch,
        inputSearch: '',
        expanded: false,

      });
    }
  }  
  showUser = (users, userName) => {
    console.log(users.find((elem)=> elem.username === userName ));
    this.props.getUser(users.find((elem)=> elem.username === userName ));
    return users.find((elem)=> elem.username === this.state.userName ) ;
  }

  render() {
    const {  userName, inputSearch } = this.state;
    const { users, user  } = this.props;
    const renderSelectOptions = this.renderSelectOptions();
    const showUser = this.showUser(users, userName);

    return (
      <div className="userPage">
        <CssBaseline />
        <Header 
            onChange={this.handleChangeSearch}
            value={inputSearch}            
            onKeyPress={this.findUser}
        />
        <div className="userContent">
        <BaseSelect             
              value={userName}
              renderSelectOptions={renderSelectOptions}
              handleChange={this.handleChange}
        />
       
        {(user) ? <UserCard user={user}
                  handleChange={this.handleChange}
                  expanded={this.state.expanded}
                  handleExpandClick= {this.handleExpandClick} 
                  />:<h1>&lt; empty &gt;</h1>}
        
                  
        </div>
        <Footer />
      </div>
    );
  }
}

export default connect(
  ({ users, user }) => ({
    users,
    user,
  }),
  { getData, getUser }
)(UserPage);
