import React from "react";
import PropTypes from "prop-types";

const Image = ({ src, alt, width, height}) => {
  return (
    <img
      src={src}
      alt={alt}      
      width={width}
      height={height}
    />
  );
};

Image.propTypes = {
  src: PropTypes.string,
  alt: PropTypes.string,
  width: PropTypes.number,
  height: PropTypes.number,
};

Image.defaultProps = {
  src: "",
  alt: "image",
  width: 100,
  height: 100,
};

export default Image;