import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import IconButton from "@material-ui/core/IconButton";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Collapse from "@material-ui/core/Collapse";
import { parsePhoneNumberFromString } from 'libphonenumber-js'
import MapWithAMarker from "..//Map/map";
import Typography from "@material-ui/core/Typography";
import classnames from "classnames";

const styles = theme => ({
  card: {
    flex: "0 0 auto",
    alignSelf: "center",
    margin: "10px",
    width: 600,
    minHeight: 400
  },
  content: {
    minWidth: 400,
    display: "grid",
    gridTemplateColumns: "40% 60%",
    gridAutoRows: "50px",
    alignItems: "center"
  },
  field: {
    padding: 10,
    textAlign: "end",
    color: "#757575"
  },
  value: {
    padding: 10,
    textAlign: "start",
    color: "#757575"
  },
  actions: {
    display: "flex"
  },
  expand: {
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest
    })
  },
  expandOpen: {
    transform: "rotate(180deg)"
  },
  address: {
    textAlign: "center"
  },
  map: {
    position: "static",
    width: "90%",
    height: "90%"
  }
});

const UserCard = ({
  classes,
  user,
  handleExpandClick,
  expanded
}) => (
  <Card className={classes.card}>
    <CardContent className={classes.content}>
      <div>
        <Typography className={classes.field} variant="subtitle1">
          {" "}
          Name:{" "}
        </Typography>
      </div>
      <div>
        <Typography className={classes.value} variant="body1">
          {" "}
          {user.name}{" "}
        </Typography>
      </div>
      <div>
        <Typography className={classes.field} variant="subtitle1">
          {" "}
          Phone:{" "}
        </Typography>
      </div>
      <div>
        <Typography className={classes.value} variant="body1">
        {(( ) => {

        const phoneNumber = parsePhoneNumberFromString(user.phone, 'US');
        console.log(user.phone);
        return  phoneNumber.formatInternational();
        })()}
 
           
        </Typography>
      </div>
      <div>
        <Typography className={classes.field} variant="subtitle1">
         Email:
        </Typography>
      </div>
      <div>
        <Typography className={classes.value} variant="body1">
          {user.email}
        </Typography>
      </div>
      <div>
        <Typography className={classes.field} variant="subtitle1">
          Website:
        </Typography>
      </div>
      <div>
        <Typography className={classes.value} variant="body1">
          {" "}
          {user.website}{" "}
        </Typography>
      </div>
      <div>
        <Typography className={classes.field} variant="subtitle1">
         Company:
        </Typography>
      </div>
      <div>
        <Typography className={classes.value} variant="body1">
          {user && user.company && user.company.name}
        </Typography>
      </div>

      <div>
        <Typography className={classes.field} variant="subtitle1">
          Address:
        </Typography>
      </div>
      <div>
        <IconButton
          className={classnames(classes.expand, {
            [classes.expandOpen]: expanded
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="Show more"
        >
          <ExpandMoreIcon />
        </IconButton>
      </div>
    </CardContent>

    <Collapse
      className={classes.address}
      in={expanded}
      timeout="auto"
      unmountOnExit
    >
      <CardContent>
        <Typography paragraph>
          {user && user.address && user.address.zipcode + " "}
          {user && user.address && user.address.city + " "}
          {user && user.address && user.address.street + " "}
          {user && user.address && user.address.suite}
        </Typography>
        
          <MapWithAMarker
            containerElement={<div style={{ height: `400px` }} />}
            mapElement={<div style={{ height: `100%` }} />}
            geo={user && user.address && user.address.geo}
          />
      
      </CardContent>
    </Collapse>
  </Card>
);

//(()=>console.log(user.company.name))()
UserCard.propTypes = {
  classes: PropTypes.object.isRequired,
  user: PropTypes.object
};

UserCard.defaultProps = {
  user: {},

  classes: {}
};
export default withStyles(styles)(UserCard);
